<template>
  <div id="atable">
    <p style="padding: 10px;text-align: left">
      <Button type="primary" size="small" @click="activitiesModal = true">{{title}}</Button>
    </p>
    <div style="height:calc(100% - 30px);overflow: auto">
      <Table :columns="columns1" :data="detailData" highlight-row></Table>
    </div>
    <!-- 人类活动列表-->
    <Modal
      v-model="activitiesModal"
      title="人类活动列表"
      @on-cancel="handleCancel"
      width="1000">
      <div id="highlight">
        <Table border stripe :columns="aColumns1"
               :data="dataList" highlight-row :height="aTableHeight"
               ref="activitiesMultipleTable"
               @on-current-change="currentChange"></Table>
        <div class="page_box" style="width: 100%;padding: 15px;text-align: right">
          <Page :total="aTotal" :page-size="aPageSize" :current.sync="aPageNum" @on-change="aPageChange" show-total/>
        </div>
      </div>
      <div slot="footer">
        <i-button type="text" @click="activitiesModal = false" class="mr10">取消</i-button>
        <Button type="primary" @click="selectActivities">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import { getRemoteList, getAudit } from '@/api/remoteSensing'
export default {
  name: 'hello',
  data () {
    return {
      /*  */
      columns1: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '红线斑块名称',
          align: 'center',
          key: 'hxcode'
        },
        {
          title: '人类活动面积',
          align: 'center',
          key: 'insectarea'
        },
        {
          title: '总面积',
          align: 'center',
          key: 'hxarea'
        }],
      /* 表格数据 */
      aColumns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.aPageNum - 1) * this.aPageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '说明',
          align: 'center',
          minWidth: 250,
          key: 'remark'
        },
        {
          title: '底图服务',
          align: 'center',
          width: 300,
          key: 'url'
        }
      ],
      detailData: [],
      activitiesModal: false,
      actList: '',
      dataList: [],
      aPageNum: 1,
      aPageSize: 10,
      aTotal: 0,
      title: ''
    }
  },
  async mounted () {
    await this.getTableList()
    // this.getMapTableList()
  },
  methods: {
    /* 获取详情信息 */
    async getMapTableList (id) {
      await getAudit({
        data: {
          id: id.toString()
        }
      }).then(res => {
        if (res.data.code === '0000') {
          if (res.data.data.out === '') {
            this.detailData = []
          } else {
            let features = JSON.parse(res.data.data.out).value.features
            let arr = []
            for (let item of features) {
              arr.push(item.attributes)
            }
            this.detailData = arr
          }
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '获取对比详情失败', desc: res.data.msg })
        }
      })
    },
    /* 单选表格触发 */
    currentChange (val) {
      this.actdataList = val
    },
    /* axios: 获取人类活动列表数据 */
    async getTableList () {
      await getRemoteList({
        data: {
          pageSize: this.aPageSize,
          pageNum: this.aPageNum
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.dataList = res.data.data.rows
          this.aTotal = res.data.data.total
          this.actdataList = this.dataList[0]
          if (this.dataList.length > 0) {
            this.dataList[0]._highlight = true
            this.title = res.data.data.rows[0].name
            this.getMapTableList(this.actdataList.id)
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 确定 */
    selectActivities () {
      this.getMapTableList(this.actdataList.id)
      this.title = this.actdataList.name
      this.activitiesModal = false
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  #atable{
    height: 100%;
  }
</style>
<style lang="scss">
  #highlight{
    .ivu-table-row-highlight td{
      background-color: #2d8cf0!important;
      color: #fff!important;
    }
  }
</style>

